export default {
	data() {
		return {
			active: 0,
			selectArr: [{
					select_index: [0, 3, 2]
				},
				{
					select_index: []
				},
			],
			title: ["景区", "行政区"],
			content: [{
					arr: ["商圈", "1KM", "3km", "5km", "10km"]
				},
				{
					arr: ["行政区", "1KM", "3km", "5km", "10km"]
				}
			]
		}
	},
	methods: {
		switchover(val) {
			this.active = val
		},
		select(val, active) {
			var _this = this
			var selectArr = _this.selectArr[active].select_index
			let i = selectArr.indexOf(val);
			if (i < 0) {
				selectArr.push(val)
			} else {
				selectArr.splice(i, 1)
			}
			_this.selectArr[active].select_index = selectArr
		},
	}
}
